html {
  background-color: black;
}

* {
  padding: 0;
  margin: 0;
}

.App {
  font-family: sans-serif;
  text-align: center;
  display: flex;
  justify-content: center;
  align-content: center;
  background-color: black;
  font-size: min(5vw, 5vh);
  user-select: none;
  --saturate-not-active: 15%;
  --saturate-active: 100%;
}

.game-state {
  width: min(100vw, 100vh);
  height: min(100vw, 100vh);
  background-color: #000d;
  transform: translateZ(200px);
  border-radius: 100%;
  overflow: hidden;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
}

.game-state div {
  height: 50%;
}

.game-state .error {
  height: 20%;
}

.game-cicle {
  color: Bisque;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.game-cicle div {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  width: 50%;
}

.game-description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
}
.game-description div {
  display: flex;
  justify-content: center;
}

.game-do {
  color: Coral;
  font-size: 1.5em;
}

.game-start {
  color: Chartreuse;
  background-color: SeaGreen;
  border-radius: 10% / 20%;
  padding: 0em 2em;
  align-items: center;
  justify-self: center;
  align-self: center;
}

.game-start:hover {
  text-decoration: underline;
}

.game-back-stage {
  display: flex;
  justify-content: center;
  align-items: center;
}
.game-back-stage div {
  height: min-content;
  color: LightSteelBlue;
  background-color: SlateBlue;
  border-radius: 10% / 50%;
  padding: 0.5em 1em;
}

.game-back-stage div:hover {
  text-decoration: underline;
}

