.circle {
  position: absolute;
  width: min(100vw, 100vh);
  height: min(100vw, 100vh);
  border-radius: 100%;
  overflow: hidden;
}

.quart {
  position: absolute;
  width: min(50vw, 50vh);
  height: min(50vw, 50vh);
  border-top-left-radius: 100%;
  transform-origin: bottom right;
  transition: rotate 1s ease-in-out;
  filter: saturate(var(--saturate-not-active));
}

.hidder {
  position: absolute;
  width: min(50vw, 50vh);
  height: min(50vw, 50vh);
  border-top-left-radius: 100%;
  overflow: hidden;
  pointer-events: none;
}

.q1 {
  pointer-events: initial;
  background-color: var(--q1);
  transform: translateZ(var(--q1-translateZ)) rotate(var(--q1-rotate));
}
