.center {
  width: 50%;
  height: 50%;
  margin: 25%;
  transform: translateZ(10px);
  border-radius: 100%;
  overflow: hidden;
  display: flex;
}

.game-state-board {
  align-self: center;
  justify-self: center;
  width: 100%;
  color: white;
  transform: translateZ(300px);
  pointer-events: none;
}

.center-quart {
  position: absolute;
  width: 50%;
  height: 50%;
  border-top-left-radius: 100%;
  transform-origin: bottom right;
  transition: rotate 1s ease-in-out;
  filter: saturate(var(--saturate-not-active));
}

.active-section {
  filter: saturate(var(--saturate-active));
}

